import { Typography } from "@mui/material"
import { RankingDetectionsPanelStyled } from "./StoryCardRanking.styles"

type TabPanelProps = {
  children?: React.ReactNode
  index: number
  value: number
  detections: { entities: Array<string>; topics: Array<string> }
}

const RankingDetectionsPanel = (props: TabPanelProps) => {
  const { detections, value, index, ...other } = props
  const { entities, topics } = detections

  return (
    <RankingDetectionsPanelStyled
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <>
          {entities.length ? (
            <>
              <Typography>
                ENTIT{entities.length > 1 ? "IES" : "Y"} ({entities.length})
              </Typography>
              <div>
                {entities.map(entity => (
                  <Typography key={entity}>{entity}</Typography>
                ))}
              </div>
            </>
          ) : null}
          {topics.length ? (
            <>
              <Typography>
                TOPIC{topics.length > 1 && "S"} ({topics.length})
              </Typography>
              <div>
                {topics.map(topic => (
                  <Typography key={topic}>{topic}</Typography>
                ))}
              </div>
            </>
          ) : null}
        </>
      )}
    </RankingDetectionsPanelStyled>
  )
}

export { RankingDetectionsPanel }
